html {
  box-sizing: border-box; }

*, *::after, *::before {
  box-sizing: inherit; }

* {
  margin: 0px;
  padding: 0px; }

html {
  font-weight: 300;
  font-size: 12px; }

body {
  color: #666;
  font-family: 'Oswald', sans-serif;
  font-size: 1em;
  line-height: 1.62em;
  height: 100%;
  background-color: transparent; }

h1, h2, h3, h4, h5 {
  margin-bottom: 0.62em;
  margin-top: auto; }

a {
  color: #009;
  text-decoration: none;
  cursor: pointer; }

a:hover {
  color: #006; }

canvas {
  display: block; }

[ng\:cloak], [ng-cloak], [data-ng-cloak], [x-ng-cloak], .ng-cloak, .x-ng-cloak {
  display: none !important; }

