.incompatible-browser {
  display: none;
  position: relative;
  width: 100%;
  text-align: center; }

.no-js .incompatible-browser {
  display: block; }

.incompatible .incompatible-browser {
  display: block; }

.loader {
  display: none;
  text-align: center; }

.version {
  position: fixed;
  right: 0;
  bottom: 0;
  max-width: 100%;
  padding: 0.5em;
  background: #eee;
  color: #666;
  opacity: 0.9;
  z-index: 9999; }
  .version span {
    color: #aaa; }
